import React, { Component } from 'react';
import { connect } from "react-redux";
import moment from 'moment';
import { Link } from 'react-router-dom';

class BillingOverview extends Component {
    state = {
        expiresAt: null,
        plan:"UGC Suit"
    }
    componentDidMount() {
        const { userData } = this.props
        const { expiresAt } = this.state
     
        if (userData && userData.user && userData.UserPlan && Object.keys(userData.UserPlan).length>0 && expiresAt == null) {
            const myMoment = moment(new Date(userData.UserPlan.expiresAt * 1000))
            this.setState({ expiresAt: (myMoment.diff(moment(), 'days')),plan:`UGC Suit ${userData.UserPlan.Plan.name}` })
        }
    }
    render() {
        const { expiresAt ,plan} = this.state

        return <div className="container container-xs">
            <div className="mt-xxl-3 mt-2">
                <h3 className="fs-6 text-gray-800">Overview</h3>
            </div>
            <div className="card mb-xxl-4 mb-3">
                <div className="card-body px-3 py-3 px-xxl-4 py-xxl-5 px-xl-3 py-xl-4">
                    <div className="row">
                        <div className="col-lg-3 col-sm-6">
                            <div className="mb-lg-0 mb-3">
                                <p className="mb-lg-4 mb-2 text-gray-600 fs-7">Current Plan</p>
                                <h6 className="text-gray-800">{plan}</h6>
                            </div>
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            <div className="mb-lg-0 mb-3">
                                <p className="mb-lg-4 mb-2 text-gray-600 fs-7">Status</p>
                                <h6 className="text-gray-800">{expiresAt && expiresAt>0 ? `Active` : `In Active`}</h6>
                            </div>
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            <div className="mb-lg-0 mb-3">
                                <p className="mb-lg-4 mb-2 text-gray-600 fs-7">Days left on trial</p>
                                <h6 className="text-gray-800">{expiresAt ? expiresAt : 0} Days</h6>
                            </div>
                        </div>
                        <div className="col-lg-3 col-sm-6 d-none">
                            <div className="text-lg-center mt-2 mt-lg-3">
                                <Link to={'price'} className="btn btn-primary btn-lg">
                                    <span className="svg-icon">
                                        <svg id="Group_1" data-name="Group 1" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                            <path d="M0,0H24V24H0Z" fill="none" />
                                            <path d="M4.586,24a1.715,1.715,0,0,1-1.027-.345l-.009-.007a1.755,1.755,0,0,1-.693-1.8l.863-4.172L.584,14.8a1.8,1.8,0,0,1,1-3.112l4.231-.461L7.57,7.343a1.8,1.8,0,0,1,3.265,0L12.59,11.2l4.227.46a1.828,1.828,0,0,1,1.435,1.07l.064.185A1.7,1.7,0,0,1,17.8,14.76l-3.151,2.892.863,4.171,0,.01a1.7,1.7,0,0,1-.689,1.788l-.008.006a1.786,1.786,0,0,1-1.934.109L9.2,21.632,5.488,23.759a1.919,1.919,0,0,1-.9.241Zm4.633-4.107a1.263,1.263,0,0,1,.652.181l4.1,2.325-.956-4.6a1.367,1.367,0,0,1,.408-1.266l3.481-3.174-4.671-.511a1.431,1.431,0,0,1-1.091-.806L9.2,7.776,7.261,12.064a1.4,1.4,0,0,1-1.088.779L1.5,13.355l3.483,3.176a1.321,1.321,0,0,1,.406,1.262L4.432,22.4l4.093-2.319.006,0a1.346,1.346,0,0,1,.676-.183ZM2.424,6.288,1.2,3.768A.858.858,0,0,1,1.6,2.621a.837.837,0,0,1,.376-.088.85.85,0,0,1,.77.48L3.961,5.531A.87.87,0,0,1,3.57,6.678a1.031,1.031,0,0,1-.386.092A.849.849,0,0,1,2.424,6.288Zm12.42.387,0,0a.86.86,0,0,1-.4-1.127l1.221-2.5a.861.861,0,0,1,1.548.755L16,6.279a.864.864,0,0,1-1.153.4ZM9.2,4.485a.86.86,0,0,1-.855-.838V.861a.861.861,0,0,1,1.722,0V3.624a.865.865,0,0,1-.862.861Z" transform="translate(3 0)" fill="currentColor" />
                                        </svg>
                                    </span>
                                    Upgrade now
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authenticated.userData
    }
}

export default connect(mapStateToProps)(BillingOverview);